.users {
    padding: 10px 0 10px 10px;
    flex: 1 1 auto;
    color: #666;
}

.users__item {
    margin-bottom: 20px;
}

.users__title {
    margin-top: 0;
    font-size: 24px;
}

.users__left {
    margin-right: 10px;
    width: 100px;
    flex-shrink: 0;
    text-align: center;
}

.users__pic {
}

.users__photo {
    width: 75px;
    height: 75px;
    margin: 0 auto;
    border-radius: 50%;
}

.users__no-photo {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 75px;
    height: 75px;
    margin: 0 auto;
    border-radius: 50%;
    background-color: #ccc;
}

.users__btn {
    padding: 5px 10px;
    font-size: 20px;
    text-decoration: none;
    color: #fff;
    border: 0 none;
    outline: 0;
    cursor: pointer;
    background-color: green;
}

.users__btn:disabled {
    background-color: #ccc;
}

.users__right {
    width: 100%;
    padding: 10px;
    border-radius: 5px;
    border: 2px solid #eee;
}

.users__block {
    height: 100%;
}

.users__info {
    flex-direction: column;
}

.users__name {
    margin: 0;
    font-size: 20px;
    font-weight: bold;
}

.users__status {
    margin: auto 0 0;
}

.users__location {
    margin-left: auto;
    font-size: 20px;
    font-weight: bold;
}

.users__country {
    margin: 0 0 10px;
}

.users__city {
    margin: 0;
}