.pagination {
}

.pagination__item {
    display: inline-block;
    margin: 0 5px;
}

.pagination__item_current {
    padding: 5px 10px;
    margin: 0 5px;
    font-weight: bold;
    border: 1px solid #666;
}

.pagination__link {
    display: inline-block;
    padding: 5px 10px;
    text-decoration: none;
    border: 1px solid #eee;
}